.App-wrapper {
  display: grid;
  
  grid-template-columns: 150px 1fr;
  grid-template-rows: 100px 1fr;
    grid-template-areas: 
        "header header"
        "navbar main-content";
}

.App-wrapper-content {
  grid-area: main-content;
  border: 2px solid rgb(255, 0, 255);
  margin: 3px 3px;
  background-color: rgb(195, 255, 127);
  border-radius: 10px;
  }
  
  




